.sidebar-menu {
  text-align: center;

  .menu-item {
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: transparent;
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
    border-radius: 5px;
    font-size: 15px;

    &:hover,
    &.active {
      background: linear-gradient(90deg, $fifth-color, $sixth-color);
      background-size: 400% 400%;

      -webkit-animation: CSSGradientAnimator 30s ease infinite;
      -moz-animation: CSSGradientAnimator 30s ease infinite;
      animation: CSSGradientAnimator 30s ease infinite;
      text-decoration: underline;
    }
  }
}

@-webkit-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@-moz-keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

@keyframes CSSGradientAnimator {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}

.modal-adaptive-size {
  min-width: 80%;

  // Below Desktop
  @media (max-width: 992px) {
    min-width: 100%;
    margin: 0;
    top: 0;
  }

  @media (max-width: 600px) {
    min-height: 100%;
    min-width: 100%;
    margin: 0;
    top: 0;
  }

  .ant-modal-content {
    @media (max-width: 992px) {
      min-height: 100vh !important;
      margin: 0;
      top: 0;
    }

    @media (max-width: 600px) {
      min-height: 100vh !important;
      min-width: 100%;
      margin: 0;
      top: 0;
    }
  }
}

